
.container{
        &__header{
            @media(min-width: 320px){
        display: flex;
        text-align: left;
        padding: 4rem 0 0 5rem;
        font-weight: 800;
        letter-spacing: 2px;
        text-transform: capitalize;
        font-size: 2rem;
        color:#1890ff;
        //font-family: 'Open Sans', sans-serif;
        width: 100%;
        flex-direction: column;
        font-family: sans-serif;
    }
            @media(min-width:768px){
                font-size: 3rem;
            }
            @media(min-width:1024px){
                width: 40%;
                font-size: 4.75rem;
            }
    & p{
        @media(min-width: 320px){
        color: white;
        text-transform: capitalize;
        font-weight: 500;
        font-size: 1rem;
        }
        @media(min-width: 768px){
    
            font-size: 1rem;
            }
            @media(min-width: 1024px){
                font-size: 1.5rem;
                }
    }}



@keyframes popout {
    0%{
        transform: scale(0.5)
    }
    50%{
        transform: scale(0.75)
    }
    100%{
        transform: scale(1)
    }
}

    &__form{
    
        @media(min-width: 320px){
        display: flex;
        width: 100%;
        padding: 2rem 2rem 2rem 2rem;
        flex-direction: column;
        align-items: center;    
        height: auto;

        }
        @media(min-width: 768px){
            flex-direction: row;
            display: flex;
            width: 100%;
            justify-content: center;
            height: 60%;
            }
            @media(min-width: 1024px){
                width: 60%;
                height: 70%;
            }
    }
    &__row{
        @media(min-width:320px){
       // background-color: #001529;
       background-color: white;
       display: flex;
        background-image: url('../../images/mybackground.svg');
        background-size: cover;
        background-attachment: fixed;
        height: auto;
        justify-content: center;
        position: relative;
        
        }
        @media (min-width: 768px){
        }
        @media (min-width: 1024px){
            background-attachment: fixed;
            background-position: bottom 0% right 62%;
            clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
            height: 80vh;
            padding: 4rem 0 4rem 0;
                   }
    &__two{
        @media(min-width: 320px){
            position: relative;
        }
            @media(min-width:768px){
                height: auto;
            }
            @media(min-width: 1024px){
                min-height: 75vh;
            }
          
        }
}
    &__column{

        @media(min-width:320px){
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        }
        @media(min-width:768px){
            display: flex;
            justify-content: center;
            
            }
    &__two{
        @media(min-width: 320px){
            padding: 4rem 2rem 4rem 2rem;
            background-image: url('../../images/thiswork.svg');
            display: none;
            background-repeat: no-repeat;
            background-position: center;
        }
        @media(min-width: 768px){
            display: flex;
        }
        @media(min-width: 1024px){
            display: flex;
        }
    }
    &__three{
        @media(min-width: 320px){
        }
        @media(min-width: 768px){
        }
        @media(min-width: 1024px){
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            padding: 0 0 0 5rem;
        }
    }
    }
}
.footer__container{
    &__technologies{
        display: flex;
        justify-content: space-around;
    & > div {
        margin: 0 1rem 0 1rem;
    } }
    &__about{
        color: white;
        font-size: 0.85rem;
        line-height: 2rem;
    }
    &__contact{
    }
}

.footer__column{
    &__two{
        @media(min-width: 320px){
            flex-direction: column;
            align-items: center;
            display: flex;
            box-shadow:  0 0 2px 0 grey;
            margin: 0.5rem 0 0.5rem 0;
            padding: 0.5rem;
            width: 100%;
        }
        @media(min-width: 768px){
            box-shadow: none;
            margin: 0.5rem 0 0.5rem 0;
            padding: 0.5rem;
            min-height: 35vh;
        }
        @media(min-width: 1024px){
            box-shadow: none;
            min-height: 47vh;

        }
    &.information{
        padding: 0 0.75rem 0.75rem 0.65rem;
        font-size: 0.75rem;
    & div + div {
        font-size: 0.75rem;
    }}}
}
.footer__row{
    @media(min-width:320px){
        justify-content: space-evenly;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 1rem;
        width: 100%;
    background-color: #001529;
    
    }
    @media(min-width: 768px){
        flex-direction: row;
    }
    @media(min-width: 1024px){
        min-height: 45vh;
        flex-direction: row;
    }
    
}
.footer{
    &__header{
        line-height: 2rem;
        border-bottom: solid white 0.5px;
        font-size: 1rem;
        color: whitesmoke;
        text-align: center;
    & ~ p {
            font-style: italic;
        padding: 0.1rem;
        color: white;
    }
&__main{
    padding: 1rem;
    color: white;
    font-size: 1.25rem;
}}
}
.icon{
    &__contact{
        margin-right: 1rem;
        color: white;
        font-size: 1.75rem;
    }
}
.footer{
    &__icon__container{
        padding: 0.5rem;
        justify-content: flex-start;
        color: #1890ff;
        font-size: 0.75rem;
        display: flex;
        align-items: center;
     }
}